.App {
  background-color: #2b2b2b;
  color: aliceblue;
  padding-bottom: 3rem;
}

.featurette-divider {
  margin: 5rem 0;
}

.featurette img {
  width: 100%;
}
.featurette h2 {
  font-weight: 300;
  line-height: 1;
  letter-spacing: -0.05rem;
  font-size: 50px;
  margin-bottom: 15px;
}

.imagette h2 {
  margin-top: 15px;
  margin-bottom: 5px;
}

.section-title h1 {
  text-align: center;
  font-size: 75px;
  border-radius: 10px;
  border-top: aliceblue 2px solid;
  padding-top: 20px;
  padding-bottom: 20px;
}

/* Language card */
.language-card div {
  background-color: #2b2b2b;
  margin-bottom: 10px;
  border-color: aliceblue;
  min-width: 30%;
}
.language-card img {
  height: 256px;
  width: 256px;
}
.language-card .body-card {
  margin-bottom: 0px;
}

.language-card-overflow {
  overflow-x: scroll;
  max-height: 60%;
  max-width: 100%;
}

.rounded-pill {
  border: solid 2px;
  border-radius: 5px;
  width: min-content;
  padding: 2px;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 20px;
  margin-right: 10px;
}

